body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about, .configuration, .testimonials {
  padding: 60px 80px;
  font-size: 2em; }

@media (max-device-width: 768px) {
  .about, .configuration, .testimonials {
    padding: 30px 40px;
    font-size: 1em; } }

.button {
  border: 0;
  margin: 0;
  border-radius: 10px;
  padding: 15px 35px;
  font-size: 1em;
  font-weight: bold;
  background-color: transparent;
  border: solid 3px black;
  color: black;
  cursor: pointer; }
  .button.white {
    color: white;
    border: solid 3px white; }

@media (max-device-width: 1024px) {
  .button {
    width: 100%;
    margin-bottom: 10px; } }

.margin-right {
  margin-right: 10px; }

.navigation {
  width: 100vw;
  height: 70px;
  font-weight: bold;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  background: linear-gradient(40deg, #d800ff7a, #399faf);
  border-top: solid 4px white; }
  .navigation .link {
    margin-top: 10px;
    width: 300px;
    text-align: center;
    font-size: 2em;
    color: white;
    text-decoration: none; }

.banner {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background: linear-gradient(40deg, #d800ff7a, #399faf);
  color: white; }
  .banner .header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Staatliches';
    font-size: 8em; }

@media (max-device-width: 768px) {
  .banner .header {
    font-size: 4em; } }

.about {
  text-align: justify; }
  .about p {
    margin-bottom: 40px; }

.configuration .line {
  margin: 40px 0; }
  .configuration .line input {
    margin-left: 10px;
    font-size: 1em;
    border: none;
    border-bottom: solid 2px black;
    min-width: 20px; }

@media (max-device-width: 1216px) {
  .configuration .line input {
    font-size: 1em;
    width: 100%;
    margin-left: 0px;
    margin-top: 10px; } }

